'use strict';

// MODULES //

import { isPrimitive as isNumber } from '@stdlib/assert-is-number';
import isAccessorArray from '@stdlib/array-base-assert-is-accessor-array';
import isComplexDataType from '@stdlib/ndarray-base-assert-is-complex-floating-point-data-type';
import accessorSetter from '@stdlib/array-base-accessor-setter';
import setter from '@stdlib/array-base-setter';
import zeros from '@stdlib/array-base-zeros';
import buffer from '@stdlib/ndarray-base-buffer';
import ndarray from '@stdlib/ndarray-base-ctor';
import resolveStr from '@stdlib/ndarray-base-dtype-resolve-str';
import format from '@stdlib/string-format';


// MAIN //

/**
* Broadcasts a scalar value to an ndarray having a specified shape.
*
* @param {*} value - scalar value
* @param {*} dtype - output array data type
* @param {NonNegativeIntegerArray} shape - output array shape
* @param {string} order - memory layout (either row-major or column-major)
* @throws {TypeError} second argument must be a recognized data type
* @returns {ndarray} ndarray
*
* @example
* import getShape from '@stdlib/ndarray-shape';
* import getDType from '@stdlib/ndarray-dtype';
*
* var x = broadcastScalar( 1.0, 'float64', [ 2, 2 ], 'row-major' );
* // returns <ndarray>
*
* var sh = getShape( x );
* // returns [ 2, 2 ]
*
* var dt = String( getDType( x ) );
* // returns 'float64'
*
* var v = x.get( 0, 1 );
* // returns 1.0
*/
function broadcastScalar( value, dtype, shape, order ) {
	var buf;
	var set;
	var N;

	buf = buffer( dtype, 1 );
	if ( buf === null ) {
		throw new TypeError( format( 'invalid argument. Second argument must be a recognized data type. Value: `%s`.', dtype ) );
	}
	dtype = resolveStr( dtype );
	if ( isComplexDataType( dtype ) && isNumber( value ) ) {
		value = [ value, 0.0 ]; // note: we're assuming that the ComplexXXArray setter accepts an array of interleaved real and imaginary components
	}
	if ( isAccessorArray( buf ) ) {
		set = accessorSetter( dtype );
	} else {
		set = setter( dtype );
	}
	set( buf, 0, value );
	N = shape.length || 1;
	return new ndarray( dtype, buf, shape, zeros( N ), 0, order );
}


// EXPORTS //

export default broadcastScalar;
